import React, {useState, useRef, useEffect} from 'react';
import DialogBox from '../DialogBox/DialogBox';

import './Avatar.css';

import border from '../../../img/border.svg';
import maw_walking from '../../../img/maw/maw_walking.gif';
import maw_waving from '../../../img/maw/maw_waving.gif';
import maw_standing from '../../../img/maw/maw_standing.gif';
import maw_talking from '../../../img/maw/maw_talking.gif';
import maw_poked from '../../../img/maw/maw_poked.gif';

const Avatar = ({visitor, dialogText, setDialogText, currentAction, setCurrentAction}) => {

    const maw = useRef();
    const [userEngaged,setUserEngaged] = useState(false);
    const [mawState,setMawState] = useState('talking');
    const [pokeCount,setPokeCount] = useState(0);

    const renderVisitorAvailableActions = () => {
        if(!userEngaged){
            return (
                <>
                <button 
                    id="action-say-hi" 
                    className="visitor-action"
                    onClick={handleAction}
                >Say Hi
                </button>
                <button 
                    id="action-say-bye" 
                    className="visitor-action"
                    onClick={handleAction}
                >Leave
                </button>
                </>
            )
        }else{
            if(currentAction==='say-hi'){
                return (
                    <>
                    <button 
                        id="action-chatter" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >I'm doing fine, thanks.
                    </button>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-contact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >How can I contact you?
                    </button>
                    </>
                )
            }
            if(currentAction==='chatter'){
                return (
                    <>
                    <button 
                        id="action-goTo-joke" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >I wanna hear a joke!
                    </button>
                    <button 
                        id="action-goTo-fact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Gimme a random fact!
                    </button>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    </>
                )
            }
            if(currentAction==='goTo-joke'){
                return (
                    <>
                    <button 
                        id="action-goTo-fact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Gimme a random fact!
                    </button>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    <button 
                        id="action-goTo-contact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >How can I contact you?
                    </button>
                    </>
                )
            }
            if(currentAction==='goTo-fact'){
                return (
                    <>
                    <button 
                        id="action-goTo-joke" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >I wanna hear a joke!
                    </button>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    <button 
                        id="action-goTo-contact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >How can I contact you?
                    </button>
                    </>
                )
            }
            if(currentAction==='goTo-work'){
                return (
                    <>
                    <button 
                        id="action-goTo-portfolio" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Can I see your portfolio?
                    </button>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-contact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >How can I contact you?
                    </button>
                    <button 
                        id="action-goTo-fact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me something interesting!
                    </button>
                    </>
                )
            }
            if(currentAction==='goTo-about'){
                return (
                    <>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    <button 
                        id="action-goTo-contact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >How can I contact you?
                    </button>
                    <button 
                        id="action-goTo-fact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me something interesting!
                    </button>
                    <button 
                        id="action-goTo-joke" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >I wanna hear a joke!
                    </button>
                    </>
                )
            }
            if(currentAction==='goTo-contact'){
                return (
                    <>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    <button 
                        id="action-goTo-joke" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >I wanna hear a joke!
                    </button>
                    <button 
                        id="action-goTo-fact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me something interesting!
                    </button>
                    </>
                )
            }
            if(currentAction==='goTo-portfolio'){
                return (
                    <>
                    <button 
                        id="action-goTo-work" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Let's talk about work!
                    </button>
                    <button 
                        id="action-goTo-about" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me more about yourself
                    </button>
                    <button 
                        id="action-goTo-contact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >How can I contact you?
                    </button>
                    <button 
                        id="action-goTo-fact" 
                        className="visitor-action" 
                        onClick={handleAction}
                    >Tell me something interesting!
                    </button>
                    </>
                )
            }
        }
    }

    const handleAction = (e) => {
        document.querySelector("#clickSound").volume = 0.4;
        document.querySelector("#clickSound").play();
        switch(e.target.id){
            case 'action-say-hi':
                setCurrentAction("say-hi");
                setUserEngaged(true);
                setDialogText(`How are you doing today${visitor&&', '+visitor}? Can I help you with anything?`);
                break;
            case 'action-say-bye':    
                setCurrentAction("say-bye");
                setDialogText("Well... Goodbye!");
                setTimeout(()=>{window.location.replace(document.referrer)},2000)
                break;
            case 'action-chatter':
                setCurrentAction("chatter");
                setDialogText("I'm really glad you're doing fine. :~<");
                break;
            case 'action-goTo-fact':
                setCurrentAction("goTo-fact");
                break;
            case 'action-goTo-joke':
                setCurrentAction("goTo-joke");
                setDialogText("Hmmmmm... let me see...");
                break;
            case 'action-goTo-about':
                setCurrentAction("goTo-about");
                setDialogText("Sure, you can read more about me on that board! :~");
                break;
            case 'action-goTo-work':
                setCurrentAction("goTo-work");
                setDialogText("Sure, take a look at the board over there.");
                break;
            case 'action-goTo-contact':
                setCurrentAction("goTo-contact");
                setDialogText("You can reach me through any of the links at the top. Or you can also drop me a line in the contact form on that board!");
                break;
            case 'action-goTo-portfolio':
                setCurrentAction("goTo-portfolio");
                setDialogText("These are some of the projects I've worked on recently.");
                break;
            default:
                return;
        }
    }
    
    const renderDialogBoxText = (dialogText) => {
        document.querySelector("#typingSound").volume = 0.4;
        return (
            <DialogBox
                text={dialogText}
            />
        )
    }

    useEffect(()=>{
        const makeJoke = async () => {
            if(Math.floor(Math.random() * Math.floor(2))){
                const res = await fetch('https://api.icndb.com/jokes/random');
                const data = await res.json();
                setDialogText(data.value.joke);
            }else{
                const res = await fetch('https://icanhazdadjoke.com/',{
                headers: {
                    'Accept': 'application/json',
                    'User-Agent': 'MAWMAWMAW (https://github.com/mawmawmaw/mawmawmaw.github.io) https://mawmawmaw.github.io/'
                  }
                });
                const data = await res.json();
                setDialogText(data.joke);
            }
        }
        const getFact = async () => {
            const res = await fetch('https://uselessfacts.jsph.pl/random.json?language=en');
            const data = await res.json();
            setDialogText(data.text);
        }
        if(currentAction === 'goTo-joke'){
            makeJoke();
        }
        if(currentAction === 'goTo-fact'){
            getFact();
        }
    },[currentAction,setDialogText])

    useEffect(()=>{
        setTimeout(()=>{
            if(mawState==='talking' && !userEngaged){
                maw.current.src = maw_waving;
                setMawState('waving');
            }
            if(mawState==='waving' && userEngaged){
                maw.current.src = maw_standing;
                setMawState('standing');
            }
        },1000);
    },[mawState,userEngaged])

    useEffect(()=>{
        var inactivityTime = () => {
            var time;
            const logout = () => {
                window.location.replace(document.referrer);
            }
            const resetTimer = () => {
                clearTimeout(time);
                time = setTimeout(logout, 300000)
            }
            window.onload = resetTimer;
            document.onmousemove = resetTimer;
            document.onkeypress = resetTimer;
        };
        inactivityTime();
    },[])

    const pokeMaw = () => {
        if(pokeCount<=4){
            document.querySelector("#pokeSound").volume = 0.1;
            document.querySelector("#pokeSound").play();
            maw.current.src = maw_poked;
            setDialogText(`Please don't poke me${visitor&&', '+visitor}!`);
            setPokeCount(pokeCount+1);
            setTimeout(()=>{
                maw.current.src = maw_standing;
                setMawState('standing');
            },1000)
        }else{
            setDialogText("Ok, this is not fun, I'm leaving.");
            document.querySelector('#bgMusic').pause();
            document.querySelector("#lostSound").volume = 0.1;
            document.querySelector("#lostSound").play();
            document.querySelector("#player-controls").style.display = 'none';
            document.querySelector("#actions-wrapper").style.display = 'none';
            document.querySelector(".right-side-wrapper").style.display = 'none';
            setTimeout(()=>{
                maw.current.src = maw_walking;
                document.querySelector("#dialog-box-container").style.display = 'none';
                document.querySelector(".avatar-shadow").style.display = 'none';
                maw.current.animate([
                    { transform: 'translateX(0px)' },
                    { transform: 'translateX(3000px)' }
                  ], {
                    duration: 5000
                  });
            },2000)
            setTimeout(()=>{
                window.location.replace(document.referrer);
            },5000)
        }
    }

    return (
        <>
            <div id="avatar-container">
                <img ref={maw} id="avatar" src={maw_talking} alt="Maw" onClick={pokeMaw}/>
                <div className="avatar-shadow"></div>
            </div>
            <div id="dialog-box-container">
                <div id="dialog-box" style={{borderImageSource: `url(${border})`}}>
                    {renderDialogBoxText(dialogText)}
                </div>
            </div>
            <div id="actions-wrapper" style={{borderImageSource: `url(${border})`}}>
                <div id="action-suggestion">
                    <p>Choose an option <i className="comment icon"></i></p>
                </div>
                <div id="actions">
                    {renderVisitorAvailableActions()}
                </div>
            </div>
        </>
    )
}

export default Avatar;