import React, { Component } from "react";
import { delay, propTypeValidation } from "./utils";
import "./DialogBox.css";

class DialogBox extends Component {
  state = {
    text: "",
    blink: false,
    hideCursor: true,
    animate: false,
    typeSpeedDelay: null,
    looping: false,
  };

  runAnimation = async (str) => {
    const textArr = typeof str == "string" && str.trim().split("");
    if (textArr) {
      this.setState({
        blink: false
      });
      let text = "";
      const typeSpeedDelay = new delay(this.props.typeSpeed || 15);
      this.setState({
        typeSpeedDelay
      });
      for (let char = 0; char < textArr.length; char++) {
        await typeSpeedDelay.getPromise();
        if(str!==this.state.text){
          document.querySelector("#typingSound").play();
          text += textArr[char];
          this.setState({
            text,
          });
        }else{ 
          if(this.state!==true){
            document.querySelector("#typingSound").pause();
            this.setState({
              blink: true
            });
          }
        }
      }
    }
  };

  animateText = async () => {
    try {
      if (!this.state.animate) {
        this.setState({
          animate: true,
        });
        const looping = this.props.loop ? this.props.loop : true;
        this.setState(
          {
            hideCursor: false,
            looping,
          },
          async () => {
            do {
              await this.runAnimation(this.props.text);
            } while (this.state.looping);

            this.props.hideCursorAfterText &&
              this.setState({
                hideCursor: true,
              });
          }
        );
      }
    } catch (error) {}
  };

  componentDidMount() {
    this.animateText();
  }

  componentWillUnmount() {
    this.setState({ looping: false });
    this.state.typeSpeedDelay && this.state.typeSpeedDelay.cancel();
  }

  render() {
    return (
      <div className={"dialog-box-wrap"}>
        <div style={{ ...this.props.textStyle }} className="dialog-box-text">
          <p>{this.state.text}</p>
          <div className={`dialog-box-pointer ${this.state.blink && "animate-pointer"} ${this.state.hideCursor ? "hide-pointer" : ""}`} style={{ backgroundColor: "rgba(0,0,0,.87)" }}></div>
        </div>
      </div>
    );
  }
}

DialogBox.propTypes = propTypeValidation;

export default DialogBox;